import Line from './lines/Line.js'
import EmptyLine from './lines/EmptyLine.js'
import NonEmptyLine from './lines/NonEmptyLine.js'
import Heading from './lines/Heading.js'
import Paragraph from './lines/Paragraph.js';
import JavaScriptOutputStream from './JavaScriptOutputStream.js';
import ListItemNoValueTag from './lines/ListItemNoValueTag.js';
import InputStream from './InputStream.js';
import HTMLOutputStream from './HTMLOutputStream.js';

//import { version } from '../package.json';

export default function render(str,includeCallback) {
//    console.log('version ' + version);

    const re_doc = /^---$/
    const re_inline_tag = /^([a-zA-Z_$][a-zA-Z\d_$]*)/

    const is = new InputStream(str);

    let os = new JavaScriptOutputStream();

    while (is.line !== false) {
        is.line.process(is,os);
        is.next();
    }

    const src = os.build();

    let f
    
    try {
        f = new Function(src);
    } catch (e) {
        console.log(src);
        throw new Error(e,src);
    }

    let sos = new HTMLOutputStream()

    try {
        const resJson = f(sos,function() {
            let name = arguments[0]
            let args = [];
            for (let i=1 ; i<arguments.length ; i++) args.push(arguments[i])
            return includeCallback(name,args)
        });

        const resHtml = sos.toString();

        return { src: src, json: resJson, html: resHtml };
    } catch (e) {
//        console.log(os.toString())
        throw e + '\n\n' + os.toString();
    }
}


