    echo(i,str) {
        this.indent(i);
        this.out += str + '\n';
    }

    p(i,str) {
      this.now(i)
      this.echo(i,`<p>${this.text(str)}</p>`)
      this.close();
    }

    ref(i,r,str) {
        
    }

    text(str) {
        let s = ''

        if (str === undefined) return s;

        for (let j=0 ; j<str.length ; j++) {
            let o = str[j];
           if (o!==undefined) {
                s += o;
           }
        }
        return s;
    }

    raw(i,str) {
        let indent = 0;
        for (let i=this.scopes.length-1 ; i>=0 ; i--) {
            let s = this.scopes[i];
            if (s.el == 'pre') {
                indent = this.peek().i + 2;
                break;
            }
        }
        this.echo(i - indent,str)
    }

    ul(i,str) {
        this.now(i,'ul')
        this.echo(i+2,`<li>${this.text(str)}</li>`);
    }

    ol(i,str) {
        this.now(i,'ol')
        this.echo(i+2,`<li>${this.text(str)}</li>`);
    }

    div(i,tag,attributes) {
        this.now(i,tag,attributes)
    }

    el(i,tag,attributes) {
        this.now(i,tag,attributes)
    }

    _el() {
        this.close();
    }

    comment(i,str) {
        this.echo(i,`<!-- ${str} -->`);
    }

    table(i) {
        this.now(i,'table')
    }

    thead(i) {
        this.now(i,'thead')
    }

    tbody(i) {
        this.now(i,'tbody')
    }

    tr(i) {
        this.now(i,'tr')
    }

    td(i,str,attrs) {
        this.echo(i,`<td${ attrs==undefined?'': attrs}>${this.text(str)}</td>`)
    }

    th(i,str,attrs) {
        this.echo(i,`<th${ attrs==undefined?'': attrs}>${this.text(str)}</th>`)
    }
    
    h1(i,str) {
        this.now(i)
        this.echo(i,`<h1>${this.text(str)}</h1>`);
        this.close();
    }

    h2(i,str) {
        this.now(i)
        this.echo(i,`<h2>${this.text(str)}</h2>`);
        this.close();
    }

    h3(i,str) {
        this.now(i)
        this.echo(i,`<h3>${this.text(str)}</h3>`);
        this.close();
    }

    h4(i,str) {
        this.now(i)
        this.echo(i,`<h4>${this.text(str)}</h4>`);
        this.close();
    }

    h5(i,str) {
        this.now(i)
        this.echo(i,`<h5>${this.text(str)}</h5>`);
        this.close();
    }

    h6(i,str) {
        this.now(i)
        this.echo(i,`<h6>${this.text(str)}</h6>`);
        this.close();
    }
    // writeln(str) {
